	module.exports = {"data":{"site":{"siteMetadata":{"title":"GREG JOHNS","author":"Greg Johns"}},"markdownRemark":{"id":"/home/greg/Projects/Greg-Johns.github.io/src/pages/2015-11-10-time/index.md absPath of file >>> MarkdownRemark","html":"<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/timewinder-2ea2f2f15aa19001f0dd070084d63881-9b0e7.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 590px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 59.5%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"time\"\n        title=\"\"\n        src=\"/static/timewinder-2ea2f2f15aa19001f0dd070084d63881-f8fb9.jpg\"\n        srcset=\"/static/timewinder-2ea2f2f15aa19001f0dd070084d63881-e8976.jpg 148w,\n/static/timewinder-2ea2f2f15aa19001f0dd070084d63881-63df2.jpg 295w,\n/static/timewinder-2ea2f2f15aa19001f0dd070084d63881-f8fb9.jpg 590w,\n/static/timewinder-2ea2f2f15aa19001f0dd070084d63881-9b0e7.jpg 600w\"\n        sizes=\"(max-width: 590px) 100vw, 590px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    \nIt’ high time to write about <em>Time</em>.</p>\n<p>While working in Verizon’s R&#x26;D lab’s I had some <em>time</em> to think about <em>time</em>. Not just as it pertains to the users experience but also as a function of the system itself, like a clock interface. This helps you think about all sorts of things as potential interfaces to measures <em>time</em>.</p>\n<p>One of the more interesting ideas I had from this prolonged thinking on <em>time</em> and interaction led to what I called TimeWinder. This was a proposed idea for an application that would let the user interact with his/her schedule on their smart phone in a quick and intuitive way.</p>\n<p>Verizon liked the idea and patented it. But alas, the <em>time</em> was not right, priorities change and the idea was never implemented.</p>\n<p><a href=\"/TimeWinder-fdc154a83213a2b80e3879ecd4279a0e.pdf\">TimeWinder</a></p>\n<blockquote>\n<p>This was pitched at the dawn of the smart phone to help set the “mood” of the interface.</p>\n</blockquote>","frontmatter":{"title":"It's about Time","date":"November 10, 2015"}}},"pathContext":{"path":"/time/"}}

